        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
    ];

    export default {
        name: "set-china-province-bar-chart",
        data() {
            return {
                inputKey: '',
                inputValue: 0,
                list: [
                    {key: '', value: 110760.94},
                    {key: '', value: 102700.00},
                    {key: '', value: 73129.00},
                    {key: '', value: 64613.00},
                    {key: '', value: 54997.07},
                    {key: '', value: 48598.80},
                    {key: '', value: 45855},
                    {key: '', value: 43903.89},
                    {key: '', value: 43443.46},
                    {key: '', value: 41781.49},
                    {key: '', value: 38700.58},
                    {key: '', value: 38680.60},
                    {key: '', value: 36206.90},
                    {key: '', value: 36102.60},
                    {key: '', value: 26181.86},
                    {key: '', value: 25691.50},
                    {key: '', value: 25115.00},
                    {key: '', value: 25002.79},
                    {key: '', value: 24500.00},
                    {key: '', value: 22156.69},
                    {key: '', value: 17826.56},
                    {key: '', value: 17651.93},
                    {key: '', value: 17360.00},
                    {key: '', value: 14083.73},
                    {key: '', value: 13797.58},
                    {key: '', value: 13698.50},
                    {key: '', value: 12311.32},
                    {key: '', value: 9016.70},
                    {key: '', value: 5532.39},
                    {key: '', value: 3920.55},
                    {key: '', value: 3005.92},
                    {key: '', value: 1902.74},
                ]
            }
        },
        computed: {
            keys() {
                return this.list.map(kv => kv.key);
            },
            options() {
                return provinceList.filter(p => this.keys.indexOf(p) === -1);
            }
        },
        methods: {
            add() {
                // make sure inputKey is a valid province name
                if (provinceList.indexOf(this.inputKey) > -1 && !Number.isNaN(this.inputValue)) {
                    this.list.push({
                        key: this.inputKey,
                        value: this.inputValue,
                    });
                    this.inputKey = '';
                    this.inputValue = 0;
                }
            },
            remove(index) {
                this.list.splice(index, 1);
            },
            display() {
                localStorage.setItem('list', JSON.stringify(this.list));
                this.$router.push('china-province-bar-chart-display');
            }
        }
    }
</script>

