export default class ChinaProvinceBarChartAlgorithms {
    /**
     * @param r: number 
     * @param lon: number 
     * @param lat: number 
     * @return {[number, number, number]}
     */
    static getXYZByLonLat = (r, lon, lat) => {
        const {sin, cos, PI} = Math;
        const lonRadian = lon / 180 * PI;
        const latRadian = lat / 180 * PI;

        const y = r * sin(latRadian); // , y

        const rOnEquatorialPlane = r * cos(latRadian); // XY, r
        const x = rOnEquatorialPlane * cos(lonRadian); // , x
        const z = rOnEquatorialPlane * sin(lonRadian); // , x

        /**
         x = R * cos(lat) * cos(lon)
         y = R * sin(lat)
         z = R * cos(lat) * sin(lon)
         */
        return [x, y, z];
    };
}